.full-screen-div {
  width: 100%;
  height: 100vh;
  background-image: url("./resources/images/Mesa de trabajo 1.webp");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@media (max-width: 768px) {
  .full-screen-div {
    background-image: url("./resources/images/Mesa de trabajo 2.webp");
  }

  .container-top {
    position: absolute;
    top: 40%;
    font-size: 2rem !important;
  }
  .container-bottom {

    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 0.7rem !important;
  }

  span {
    font-size: 1.1rem !important;
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}

span {
  color: rgb(248, 76, 0);
  font-size: 2rem;
  font-weight: 900;
}

html,
body {
  margin: 0;
  padding: 0;
  background-color: black;
}

.container-top {
  display: flex;
  flex-direction: row;
  font-size: 5rem;
}

.container-bottom {
    position: fixed;
    bottom: 0;
    width: 80%;
    display: flex;
    flex-direction: row;
    font-size: 1.2rem;
    color: white;
    padding: 30px;
    justify-content: space-between;
    align-items: flex-end;
}

.title-team {
  color: rgb(248, 76, 0);
  font-family: "AJQuadrata";
}

.title-orangutan {
  color: white;
  font-family: Rebond;
}

.text {
  color: white;
  font-family: Rebond-light;
  letter-spacing: 1px;
}
